         <div class="user">鉴于，姓名：&nbsp;&nbsp;身份证号：&nbsp;&nbsp;&nbsp;手机号：&nbsp;&nbsp;</div>
         <p>
           您（即养户，下同）与公司（即江西正邦养殖有限公司或其关联公司，下同）已经签订《生猪委托养殖合同》。
         </p>
         <p>
           为增强猪苗、饲料、兽药、设备等发放、领取的规范性和便捷性，请在养户服务号微信端勾选<span class="fbold">"我已了解并接受《养猪须知》"</span>并点击【提交】按钮；经公司审核通过后，您与公司之间即签订了本协议。
         </p>
         <div class="fbold">
           一、养户服务号的界定
         </div>
         <p>
           养户服务号是为您与公司就猪苗、兽药、饲料、设备等的领取、发放提供流程申请、审批、确认以及相互交流的微信公众号。
         </p>
         <div class="fbold">
           二、养户服务号的使用方法
         </div>
