           1.您作为用户，若是与公司新合作的养户，需通过微信端养户服务号完成开户注册程序方可登录；若是与公司正在合作的养户，需将公司提供的EAS端登记信息与养户服务号绑定后方可登录。
         </p>
         <p>
           2.您打开注册面，请务必根据提示准确填写姓名、猪舍地址、身份证号、联系电话以及介绍人等信息，否则养户服务号有权做出相应的处理，因此造成的损失将由您承担。
         </p>
         <p>3.您完成注册后，养户服务号会向您提供唯一编号的养户服务号账户。您应对您的账户的安全，以及对通过您的账户实施的行为负责。除非有法定理由，且至少应告知养户服务号，账户不得以任何方式转让、赠与或继承（与账户相关的财产权益除外）。如果发现任何人不当使用您的账户或有任何其他可能危及您的账户安全的情形时，您应当立即以有效方式通知养户服务号，要求养户服务号暂停相关服务。您理解养户服务号对您的请求采取行动需要合理时间，养户服务号对在采取行动前已经产生的后果（包括但不限于您的任何损失）不承担任何责任。</p>
         <p>4.您通过养户服务号提交领猪苗、饲料、兽药、设备等申请，经公司审核批准后，您需本人或经您授权的人来公司领取猪苗、饲料、兽药、设备等，并当场在养户服务号点击确认按钮。您领取猪苗、饲料、兽药、设备后，逾期30分钟不确认的，养户服务号系统将自动确认已领取猪苗或饲料或兽药，您对此理解并同意。</p>
         <p>
           5.养户服务号后台在每个月的第一天自动生成上一个月的领猪苗、饲料、兽药、设备的对账单，您应在当天在纸质对账单上签字确认，经告知仍拒绝签字确认的，公司做好相应记录，视为您已经认可。若当月领取猪苗、兽药、饲料、设备后，您不再领取或无需再领取的，相应的对账单将在不再领取或无需领取当天至次日生成，同样您需签字确认，否则视为您已经认可。您对本条规定理解并同意。
         </p>
         <p>
           6.养户服务号可能会与第三方合作向用户提供相关的网络服务，在此情况下，如该第三方同意承担与养户服务号同等的保护用户隐私的责任，则养户服务号有权将用户的注册资料等提供给该第三方。另外，在不透露您隐私资料的前提下，养户服务号有权对整个用户数据库进行分析。
         </p>
         <p>
           7.您与公司合作的资料及数据，养户服务号后方平台有权(全部或部分地) 使用、复制以及分析（包括但不限于注册资料、行为数据及全部展示于养户服务号微信端的各类信息）或制作其派生信息或文件资料。
         </p>
         <p>
           8.养户服务号发布的各类文件是本协议不可分割的组成部分，与本协议具有同等法律效力。您与公司基于本服务号的任何行为均受本协议的约束，否则应立即停止注册或使用本养户服务号的服务。
         </p>
         <div class="fbold">
           三、养户服务号的使用规范
         </div>
         <p>
           1.您在养户服务号上发布的信息不得违反宪法、法律以及社会公序良俗；
         </p>
         <p>
           2.您在使用养户服务号时，不得违反宪法、法律以及社会公序良俗良俗，不得违反本协议的约定。
         </p>
         <p>
           3.您本人应对微信端养户服务号账号的创建、授权、申请发起、删除等事项尽注意义务。养户服务号账号在微信端养户服务号中的任何操作均是您的行为或视同您的行为，您是基于该行为而产生的权利和义务的唯一主体。
         </p>
         <p>
           4.您违反了前述规范，养户服务号有权依据本协议的约定，做出相应处理（包括但不限于直接屏蔽、删除侵权信息）或终止向您提供服务，且无须征得您的同意或提前通知您。
         </p>
         <p>
           5.您涉嫌违反有关法律或者本协议之规定，使养户服务号遭受任何损失，或受到任何第三方的索赔，或受到任何行政管理部门的处罚，您应当赔偿养户服务号因此造成的损失及（或）发生的费用，包括律师费用。
         </p>
         <p>
           6.您如果发现您的个人信息被盗用应及时将此情况告知养户服务号，经核实，因您的信息被盗用造成损失的，在您告知前产生的或将要产生的，由您本人承担；在您告知养户服务号后才产生损失的，您在有过错的情况下才承担责任。
         </p>
         <div class="fbold">
           四、养户服务号的服务约定
         </div>
         <p>
           1.养户服务号作为信息服务微信端，不对您任何线下行为负责，任何因为浏览、搜索养户服务号微信端提供的信息而产生的费用、损失及伤害，由您自行承担，请您谨慎对待任何线下行为活动。
         </p>
         <p>
           2.养户服务号基于微信端服务的安全性，有权中止向您提供部分或全部养户服务号微信端服务，暂时冻结您的账户，待安全问题解决后及时恢复，并对中止、冻结及恢复的事实及时通知。如果网站的安全问题是由于您的违法行为引起，养户服务号有终止向您提供部分或全部养户服务号微信端服务，永久冻结（注销）您的帐户，并有权向您对损失进行索赔。
         </p>
         <p>
           3.您的账户被注销后，养户服务号没有义务为您保留或向您披露您账户中的任何信息，也没有义务向您或第三方转发任何您未曾阅读或发送过的信息。
         </p>
         <p>
           4.您与养户服务号的协议关系终止后，养户服务号仍享有下列权利：
         </p>
         <p>
           @继续保存您未及时删除的注册信息及您使用养户服务号期间发布的所有信息至法律规定的记录保存期满。
         </p>
         <p>
           @您在使用养户服务号期间存在违法行为或违反本协议和/或规则的行为的，养户服务号仍可依据本协议向您主张权利。
         </p>
         <div class="fbold">
           五、养户服务号的隐私约定
         </div>
         <p>
           1.养户服务号将在养户服务号公布并不定期修订隐私权政策，隐私权政策构成本协议的组成部分。
         </p>
         <p>
           2.您知悉并同意：养户服务号可能会与第三方合作向用户提供相关的网络服务，在此情况下，如该第三方同意承担与本网站同等的保护用户隐私的责任，则养户服务号有权将用户的注册资料等提供给该第三方。另外，在不透露单个用户隐私资料的前提下，养户服务号有权对整个用户数据库进行分析并对用户数据库进行商业上的利用。
         </p>
         <p class="fbold">
           六、本协议为电子版本，其打印版本与其具有同等的法律效力。本协议在履行过程中发生争议的，应友好协商；协商不成的，提交南昌市仲裁委员会仲裁。
         </p>
       </div>
    </div>
</template>

<script>
import myHeader from "@/components/Header";
export default {
  name: "OpenText",
  data() {
    return {};
  },
  components: {
    myHeader
  },
  methods: {}
};
</script>

<style scoped lang="scss">
